import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import profilePic from './pic.jpg';
import { Link } from 'react-router-dom';

class AboutMe extends Component {
	render() {
		return(
			<div style={{width: '100%', margin: 'auto'}}>
				<Grid className='aboutme-grid'>
					<Cell col={6}>
						<img src={profilePic}
						alt="img"
						className="aboutme-img"
						/>
					</Cell>

					<Cell col={6}>
						<div className="about-me">
						<h2>About Me</h2>
						<hr/>

						<p>
						I am an Indian originated frontend web developer and software engineer based in Stockholm.
						Passionate, creative and ambitious.
						</p>
						<p>
						I firmly believing that ui/ux is the future of software development
						I always looking forward to learning new things and grow professionally and personally to become an influential future leader.
						<br />
						I mostly work with HTML/CSS | Javascript | React | NodeJs | AngularJs</p>
						<hr />

						<p>
						There is no surprise that art is the love of my life.
						I love painting and pencil skecthing but travelling and photography is also in my veins.
						I am endlessly inspired by nature, artistic visuals and adventures. I love travelling with my friends and family exploring new cultures and meeting different mindsets.
						</p>

						<p>If this sounds exciting, <Link to="/contact" style={{ textDecoration: 'none', color: 'orange', fontFamily: 'Satisfy', fontSize:'20px'}}>Get In Touch.</Link> </p>
						<div className="social-links">
							{/* LinkedIn */}
							<a href="https://www.linkedin.com/in/lakshmisowjanyanekkanti1604" rel="noopener noreferrer" target="_blank">
								<i className="fa fa-linkedin" aria-hidden="true"/>
							</a>

							{/* Github */}
							<a href="https://github.com/soujanya-nekkenti" rel="noopener noreferrer" target="_blank">
								<i className="fa fa-github" aria-hidden="true"/>
							</a>

							{/* Facebook */}
							<a href="https://www.facebook.com/Freedom-of-Life-1858550407792349/" rel="noopener noreferrer" target="_blank">
								<i className="fa fa-facebook" aria-hidden="true"/>
							</a>

						</div>
						</div>
					</Cell>
				</Grid>
			</div>
		)
	}
}

export default AboutMe;



