import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skills from './skills';

class Resume extends Component {
	render() {
		return(
				<Grid>
					<Cell col={5}>
						<h2 style={{paddingTop:'0.5em'}}>Lakshmi Sowjanya Nekkanti</h2>
						<h4 style={{color:'grey'}}>Web Developer</h4>
						<hr style={{borderTop:'3px solid #F3904F', width:'50%'}}/>
						<p>I plan to obtain a position in the fast growing software research industry with opportunities to prosper,
							where I can provide quality professional services in academia while exploring the diversities of software
							industry.<br /><br />
							Young, enthusiastic, honest, innovative, and hardworking.
							Critical thinking and analytical skills to solve real time problems elevate my standards as a professional with
							a passion to learn and adapt to new skills and technologies.</p>
						<hr style={{borderTop:'3px solid #F3904F', width:'50%'}}/>
						<h5>Address</h5>
						<p>Essinge Brogata 33,
						<br/> Stockholm 11261 </p>
						<h5>Phone</h5>
						<p>(+46)0769410678</p>
						<h5>Email</h5>
						<p>soujanya.nekkenti@gmail.com</p>
						<h5>LinkedIn</h5>
						<p>soujanya.nekkenti</p>
						<hr style={{borderTop:'3px solid #F3904F', width:'50%'}}/>
					</Cell>

					<Cell className='resume-right-col' col={7}>
						<h2>Education</h2>
						<Education
							startYear={2015}
							endYear={2016}
							schoolName='Blekinge Tekniska Hogskolan (BTH), Sweden'
							courseName='Master of Science in Software Engineering Business'
							/>
						<Education
							startYear={2011}
							endYear={2014}
							schoolName='Jawaharlal Nehru Technological University Kakinada (JNTUK), India'
							courseName='Bachelor of Technology in Computer Science and Engineering'
							/>
							<hr style={{borderTop: '3px solid #F3904F'}}/>

							<h2>Experience</h2>
							<Experience
								startMonth='Jan'
								startYear= {2017}
								endMonth='May'
								endYear={2018}
								jobName='Software Engineer'
								companyName='Qliro AB'
								jobDescription='The work includes creating a new version of an application using React framework. Frontend technologies: JavaScript6 (ECMA Script 6), React JS, Redux, ES. Backend: C# on .net framework using Visual Studio. Unit tests with 80% code coverage in frontend. As a tester, I helped the team in identifying the bugs during regression testing, documenting them and creating tickets in Jira to track them. Partially played the role as tester during critical situations. Learned using tools like Soap UI to generate data for testing and writing test cases. As part of team, I am involved in continuous integration and deployment of different services used by the system.  Creating unit tests to improve the performance and quality of code.'
								/>
							<Experience
								startMonth='May'
								startYear= {2018}
								endYear='current'
								jobName='System Developer'
								companyName='ATG'
								jobDescription='The work includes developing web application using React framework.'
								/>
								<hr style={{borderTop: '3px solid #F3904F'}}/>

								<h2>Skills</h2>
								<Skills
									skill='Javascript'
									progress={75}
									/>
									<Skills
										skill='HTML/CSS'
										progress={75}
										/>
										<Skills
											skill='React'
											progress={45}
											/>
											<Skills
												skill='AngularJS'
												progress={30}
												/>
												<Skills
													skill='Bootstrap'
													progress={50}
													/>
													<Skills
														skill='C, C#'
														progress={25}
														/>
														<Skills
															skill='Java'
															progress={40}
															/>


					</Cell>
				</Grid>
		)
	}
}

export default Resume;



