import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton} from 'react-mdl';

class Projects extends Component {
	constructor(props) {
		super(props);
		this.state ={activeTab: 0};
	}

	toggleCategories(){
		if(this.state.activeTab === 0) {
			return(
				<div className="projects-grid">
				{/* Project 1 */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'white', height: '176px', background:
					'url(https://reactjs.org/logo-og.png) center / cover'}}>React Project #1</CardTitle>
						<CardText>
							A simple player management tool written in React.
							<hr/>
							The user is be presented with a list of players received from the server, and have the option to add new players and update/delete existing ones.
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				</div>
			)
		} else if(this.state.activeTab === 1) {
			return(
				<div className="projects-grid">
				{/* Project 1 */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background:
					'url(https://cdn.colorlib.com/wp/wp-content/uploads/sites/2/angular-logo.png) center / cover'}}>Angular Project #1</CardTitle>
						<CardText>
							This a "Sports Poll" app which is a dead simple poll system around some sports events which can be fetched from a JSON file.
							<hr />
							It allows:
							To vote on each event,
							Shows another random event on browser reload,
							Calculates the poll count.
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>

				{/* Project 2 */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background:
					'url(https://cdn.colorlib.com/wp/wp-content/uploads/sites/2/angular-logo.png) center / cover'}}>Angular Project #2</CardTitle>
						<CardText>
							A simple web application that fetches the episodes of a TV-show from an API.
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>

				{/* Project 3 */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background:
					'url(https://cdn.colorlib.com/wp/wp-content/uploads/sites/2/angular-logo.png) center / cover'}}>Angular Project #3</CardTitle>
						<CardText>
							This is a very simple player management tool application.
							<hr />
							The user is be presented with a list of players received from the server, and have the option to add new players and update/delete existing ones.
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				</div>
			)
		} else if(this.state.activeTab === 2) {
			return(
				<div className="projects-grid">
				{/* Internship Project 1 */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'none'}}>Software Craftsmanship Trainee</CardTitle>
						<CardText>
							The work includes creation of a web application, Online Examinations. The admin of the website, should be able create new exams and assign them to the users. The users should be able to read the examinations, select one of them and answer them.
						</CardText>
						<CardText>
							Front end development is done using JavaScript, Angular JS and Bootstrap on Web Strom IDE; Backend development is done using Java on IntelliJ IDEA; Database: H2; Server: Gulp
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				{/* Internship project 2*/}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'none'}}>Summer Internship</CardTitle>
						<CardText>
							The work included the development of a prototype for video portal that allows use of video for knowledge sharing in software development.
						</CardText>
						<CardText>
							Technologies worked with: HTML5 in conjunction with Java Server Pages and servlets. Evaluated and designed the use of object oriented database Object DB for storing video annotations. The work concluded with a demonstration of successful operation of the video portal that was hosted on cloud servers (Docker).
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				</div>
			)
		} else if(this.state.activeTab === 3) {
			return(
				<div className="projects-grid">
				{/* University project-ASPM */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'noopener'}}>Student Shopper</CardTitle>
						<CardText>
							The work includes developing a web-based application service. This application allows members of BTH to sell and buy pre-owned products through bidding. It is developed for its high usability characteristics and the value it provides for the users.
							<hr/>
							Developed user interface using HTML5, CSS, Bootstrap UI and semantic UI frameworks. Frontend development is done using Angular JS; JQuery and Java Script. Backend development is done using Node.js, PHP using Slim framework and Java using Spring MVC. Creation of database using Mongo DB.
						</CardText>

						<CardActions border>
							<Button colored>Website</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>

				{/* v&v */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'none'}}>Verification and Validation of an Open Source Project</CardTitle>
						<CardText>
							The project involves static automated code analysis on an open source graphical Sudoku game to assure software quality using open source static tools. Later, dynamic analysis is also conducted using two dynamic testing tools. Static analysis tools used are Find Bugs and Dynamic analysis tools are CodePro and Randoop.
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				</div>
			)
		} else if(this.state.activeTab === 4) {
			return(
				<div className="projects-grid">
				{/* Master Thesis */}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'noopener'}}>Master Thesis</CardTitle>
						<CardText>
							Impact of coordination challenges on global software development projects.
							<hr/>
							The study focuses on identifying the coordination challenges faced by the practitioners when developing the software in globally distributed environment by conducting industrial survey. The results obtained are used as inputs for finding how these challenges affect the overall software quality. Further it investigates their level of impact on quality in terms of performance and productivity.
						</CardText>

						<CardActions border>
							<Button colored>Website</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>

				{/* Bachelor Thesis*/}
				<Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
					<CardTitle style={{color:'black', height: '176px', background: 'none'}}>Bachelor Thesis</CardTitle>
						<CardText>
							Anonymization of Centralized and Distributed Social Networks in Sequential Clustering.
							<hr />
							When the network data in a distributed environment is split among several data holders, it is necessary to maintain privacy. The purpose of this project is to arrive at an anonymized view of the unified network without revealing to any of the data holders, the data about the links between nodes that are controlled by other data holders. Technologies used: Java; Net Beans (IDE)
						</CardText>
						<CardActions border>
							<Button colored>Github</Button>
						</CardActions>
						<CardMenu style={{color: '#fff'}}>
							<IconButton name="share" />
						</CardMenu>
				</Card>
				</div>
			)
		}
	}

	render() {
		return(
			<div className="category-tabs">
				<Tabs activetab={this.state.activeTab}
							onChange={(tabId) => this.setState({ activeTab: tabId })}
							ripple>
						<Tab>React</Tab>
						<Tab>Angular</Tab>
						<Tab>Internships</Tab>
						<Tab>University Projects</Tab>
						<Tab>Thesis</Tab>
				</Tabs>

					<Grid>
						<Cell col={12}>
							<div className="content">{this.toggleCategories()}</div>
						</Cell>
					</Grid>

			</div>

		)
	}
}

export default Projects;



