import React, { Component } from 'react';
import profilePic from './desktop5.jpg';

class LandingPage extends Component {
	render() {
		return(
			<div style={{width: '100%', margin: 'auto'}}>
				<div className='landing-grid'>
						<img src={profilePic}
						alt="avatar"
						className="avatar-img"
						/>
				</div>

				<div className="banner-text">
					<h1>Helloo,</h1>
					<h1>Welcome come to my space</h1>
					<hr/>
					<p><span>I am Sowjanya Nekkanti, a passionate frontend developer.</span>
					<br />
					<span>And I beleive in making a difference</span></p>
				</div>

			</div>
		)
	}
}

export default LandingPage;



